header {
  display: flex;
  flex-wrap: wrap;

  &::after {
    content: '';
    display: block;
    background-color: var(--light-grey-color);
    width: 96%;
    height: 1px;
    margin: 10px auto;
  }
}

.stats {
  display: inline-block;
  text-align: center;

  margin-left: 10px;
}

.stats__btn:not(:last-child) {
  margin-right: 25px;
}

.stats__btn {
  line-height: 1.5;
  text-align: left;

  background-size: contain; // фоновая картинка уменьшается до размеров псевдоэлемента label::before (чекбокса)
  background-origin: padding-box; // какое пространство занимает фоновая картинка, при border-box занимает content, padding и border
  background-repeat: no-repeat;
  background-color: transparent;

  padding: 0 0 0 30px;
  border: none;

  font-size: 16px;
}

.stats__all-todos {
  color: black;
  background-image: url(../../images/todos-all.png);
}

.stats__undone-todos {
  color: var(--grey-color);
  background-image: url(../../images/todos-undone.png);
}

// .stats__important-todos {
//   color: var(--red-color);
//   background-image: url(../../images/todos-important.png);
// }

.stats__done-todos {
  color: var(--green-color);
  background-image: url(../../images/todos-done.png);
}

.filter-btn {
  width: 20px;
  height: 20px;
  margin: 4px 10px 0 auto;
  padding: 0;

  background-image: url(../../images/filter.svg);
  background-origin: content-box; // какое пространство занимает фоновая картинка, при border-box занимает content, padding и border
  background-size: contain;
  background-repeat: no-repeat;
  background-color: transparent;
  border: none;
}
